<template>
  <!--  -->
  <div class="top">
    <div class="top_line">
      <div></div>
      <p></p>
      <p></p>
      <p></p>
      <p></p>
      <p></p>
      <p></p>
      <p></p>
    </div>
    <div class="top_line">
      <div></div>
      <div>
        <el-select size="mini" v-model="value1" placeholder="">
          <el-option
            v-for="item in options1"
            :key="item.value1"
            :label="item.label"
            :value="item.value1"
          >
          </el-option>
        </el-select>
      </div>
      <div>
        <el-select size="mini" v-model="value2" placeholder="">
          <el-option
            v-for="item in options2"
            :key="item.value2"
            :label="item.label"
            :value="item.value2"
          >
          </el-option>
        </el-select>
      </div>
    </div>
    <div class="top_line">
      <el-checkbox v-model="checked1" label=""></el-checkbox>
      <el-checkbox
        class="choose"
        v-model="checked2"
        label=""
      ></el-checkbox>
    </div>
  </div>
  <div id="ship_myEcharts" :style="{ width: '900px', height: '400px' }"></div>
  <div class="footer">
    <div class="footer_title">
      <p></p>
      <p></p>
      <p>400</p>
    </div>
    <div class="footer_main">
      <el-table
        :data="tableData"
        border
        style="width: 100%; height: 100%"
        size="mini"
        :header-cell-style="{ background: '#f5f5f5' }"
      >
        <el-table-column prop="ranking" label="" width="">
        </el-table-column>
        <el-table-column prop="consignor" label="" width="">
        </el-table-column>
        <el-table-column prop="shipment_quantity" label="" width="">
        </el-table-column>
      </el-table>
    </div>
  </div>
  <div class="last"> 4 </div>
</template>
<script>
import { reactive, toRefs, onMounted, onUnmounted } from "vue";
import * as echarts from "echarts";
export default {
  name: "echartsBox",
  components: {},
  setup() {
    const state = reactive({
      value1: "",
      options1: [],
      value2: "",
      options2: [],
      checked1: false,
      checked2: false,
      tableData: [
        {
          ranking: "1",
          consignor: "",
          shipment_quantity: "",
        },
        {
          ranking: "2",
          consignor: "",
          shipment_quantity: "",
        },
        {
          ranking: "3",
          consignor: "",
          shipment_quantity: "",
        },
        {
          ranking: "4",
          consignor: "",
          shipment_quantity: "",
        },
      ],
    });

    // echart
    let echart = echarts;

    onMounted(() => {
      initChart();
    });

    onUnmounted(() => {
      echart.dispose;
    });

    // Echarts
    function initChart() {
      let chart = echart.init(document.getElementById("ship_myEcharts"));
      // 
      chart.setOption({
        xAxis: {
          type: "category",
          axisLabel: {
            fontSize: 16,
          },
          data: ["", "", "", ""],
        },
        tooltip: {
          trigger: "axis",
        },
        yAxis: {
          type: "value",
          min: 0,
          max: 4.5,
          splitNumber: 9,
          axisLabel: { show: false },
        },

        series: [
          {
            data: [4, 4, 3, 2],
            barWidth: 50,
            type: "bar",
            // smooth: true  
          },
        ],
      });
      window.onresize = function () {
        //
        chart.resize();
      };
    }
    return {
      ...toRefs(state),
      initChart,
    };
  },
};
</script>

<style scoped>
.top {
  margin-left: 30px;
}
.top_line {
  display: flex;
  align-items: center;
  margin-top: 10px;
  height: 40px;
}
.top_line > p {
  margin-left: 30px;
}
.el-select,
.choose {
  margin-left: 30px;
}
/* #myEcharts {
    margin-left: ;
} */
.footer {
  margin-left: 20px;
}
.footer_title {
  display: flex;
}
.footer_main {
  margin: 0 20px 20px 0;
}
.last {
  text-align: center;
  margin-bottom: 10px;
}
</style>
